import React from "react";
import Home from "./Components/Home/home";
import "./App.css";
//Material UI
import Button from '@material-ui/core/Button';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      btnText: "Send an SMS",
      btnStyle : {
        color: "white",
        backgroundColor: "black",
        borderRadius: "25px",
        marginLeft: 10,
        fontSize: 13,
        fontFamily: "Bahnschrift Light",
        pointerEvents: "auto",
      },
      text: {
        recipient: "+19199283462",
        textmessage: "Someone was on your portfolio!",
      },
    };
  }

  disableBtn = () => {
    
  }

  sendText = () => {
    // pass var within query string
    fetch(
      `https://fathomless-basin-15595.herokuapp.com/send-text?recipient=${this.state.text.recipient}&textmessage=${this.state.text.textmessage}`
    ).catch((error) => console.error(error));

    this.setState({
      btnText: "Thanks!",
      btnStyle: {
        color: "black",
        backgroundColor: "white",
        borderRadius: "25px",
        marginLeft: 10,
        fontSize: 13,
        fontFamily: "Bahnschrift Light",
        pointerEvents: "none",
      }
    });
  };

  render() {
    return (
      <React.Fragment>
        <Home />
        <div style={{ justifyContent: "center", color: "white", fontFamily: "Bahnschrift Light" , display: "flex", marginTop: 20}}>
          <p style={{fontStyle: "italic", fontSize: 17}}>
            Click on this button to let me know you were here
          </p>
          <Button onClick={this.sendText} variant="contained" style={this.state.btnStyle}>{this.state.btnText}</Button>
        </div>
      </React.Fragment>
    );
  }
}

export default App;
